
<template>
  <div class="hy_main">
    <div class="hy_container">

      <h2>문의하기</h2>

      <!-- 출력 카테고리 -->
      <select v-model="amount" class="hy_view" @change="loglist_view">
        <option value="10">10개 보기</option>
        <option value="20">20개 보기</option>
        <option value="30">30개 보기</option>
      </select>

      <button @click="inquiry" class="btn btn-primary btn_write">문의작성하기</button>
        <table class="hy_list">
          <thead class="hy_head">
            <tr>
              <th>번호</th>
              <th>제목</th>
              <th>작성자</th>
              <th>작성일</th>
              <th>답변상태</th>
            </tr>
          </thead>

          <tbody v-if="listck" class="hy_body">
            <!-- for문사용 방법 : item >> 각 배열의 값 index >> 배열 현재 index list >> 배열명  -->
            <tr v-for="(item, index) in list" v-bind:key="index" @click.prevent="goDetail(item.qa_no)"
              style="cursor: pointer;">
              <td>{{ index+1 }}</td>
              <td>{{ item.qa_title }}</td>
              <td>{{ item.user_id }}</td>
              <td>{{ item.qa_date }}</td>
              <td>{{ item.qa_answertype }}</td>
            </tr>

          </tbody>
          <tbody v-else>
            작성된 글이 없습니다.
          </tbody>
        </table>
    

      <div class="hy_serch_box">

        <select name="" id="">
          <option value="hy_title">제목</option>
          <option value="hy_content">내용</option>
        </select>

        <input type="text">
        <button @click="search($event.target)">검색</button>

      </div>

      <!-- 페이지 이동 -->
      <div class="hy_page">
        <ul>
          <li>
            <!-- 맨앞으로 가기 -->
            <router-link :to="{ path: '/11/?page=1&amount=' + amount }" @click="goFirstPage">
              <i class="fa fa-angle-double-left" aria-hidden="true">&lt;&lt;</i>
            </router-link>
          </li>

          <!-- 앞으로 가기 -->
          <li style="margin-right:5px;">
            <router-link :to="{ path: '/11/?page=' + page + '&amount=' + amount }" @click="goBeforePage">
              <i class="fa fa-angle-left" aria-hidden="true">&lt;</i>
            </router-link>
          </li>

          <!-- for문사용 방법 : item >> 각 배열의 값 index >> 배열 현재 index list >> 배열명  -->
          <div v-for="(item, index) in pageList" :key="index" class="page_btn">
            <li v-bind:class="{ 'on': item === page }">
              <router-link :to="{ path: '/11/?page=' + page + '&amount=' + amount }" @click="thisPage($event.target)">
                {{ item }}
              </router-link>
            </li>
          </div>

          <!-- 뒤로 가기 -->
          <li style="margin-left:5px;">
            <router-link :to="{ path: '/11/?page=' + page + '&amount=' + amount }" @click="goNextPage">
              <i class="fa fa-angle-right" aria-hidden="true">&gt;</i>
            </router-link>
          </li>

          <!-- 맨뒤로 가기 -->
          <li>
            <router-link :to="{ path: '/11/?page=' + realEnd + '&amount=' + amount }" @click="goLastPage">
              <i class="fa fa-angle-double-right" aria-hidden="true">&gt;&gt;</i>
            </router-link>
          </li>

        </ul>
      </div>

    </div>
  </div>
</template>

<script>
import Axios from 'axios';

export default {
  el: '#App',
  data() {
    return {

      //공용
      list: '',    //게시글리스트
      pages: '',   //pageVO
      pageList: '',  //pageVO.pageList 배열값
      detailNum: '',
      user_id: '',
      com_id: '',
      ut_no: sessionStorage.getItem("ut_no"),
      listck: true,

      //페이지이동에 필요한 초기값들
      page: 1,
      amount: 10,
      searchTitle: '',
      searchContent: '',
      prev: '',
      pageStart: '',
      pageEnd: '',
      realEnd: ''
    }
  },

  watch: {
    'amount': function () {
      this.get();
    }
  },

  methods: {
    inquiry() {
      location.href = "/13";
    },

    async get() {

      console.log(this.ut_no == 1);
      //ut_no가 유저라고 한다면 
      if (this.ut_no == 1) {

        this.user_id = sessionStorage.getItem("user_id");

        //화면에 리스트 출력을 위해 필요한 내용 전달
        let response = await Axios.get("/11/?amount=" + this.amount + "&page=" + this.page + "&searchTitle=" + this.searchTitle + "&searchContent=" + this.searchContent + "&user_id=" + this.user_id + "&com_id" + this.com_id);

        console.log(response);
        //필요한 공용 데이터를 담기
        this.list = response.data.list;
        this.pages = response.data.pageVO;
        this.pageList = this.pages.pageList;


        if (this.list.length == 0) {
          this.listck = false;
        } else {

          //페이지이동에 필요한 데이터 담기
          this.page = this.pages.page;
          this.searchTitle = this.pages.cri.searchTitle;
          this.searchContent = this.pages.cri.searchContent;
          this.prev = this.pages.prev;
          this.pageStart = this.pages.pageStart;
          this.pageEnd = this.pages.pageEnd;
          this.realEnd = this.pages.realEnd;

        }

        //ut_no가 기업이라고 한다면
      } else if (this.ut_no ==2) {
        this.com_id = sessionStorage.getItem("com_id");
        console.log(this.com_id);

        //화면에 리스트 출력을 위해 필요한 내용 전달
        let response = await Axios.get("/11/?amount=" + this.amount + "&page=" + this.page + "&searchTitle=" + this.searchTitle + "&searchContent=" + this.searchContent + "&user_id=" + this.user_id + "&com_id=" + this.com_id);

        //필요한 공용 데이터를 담기
        this.list = response.data.list;
        this.pages = response.data.pageVO;
        this.pageList = this.pages.pageList;

        if (this.list.length==0) {
          this.listck = false;
        }

        //버튼숨기기
        const btn = document.querySelector(".btn_write");
        btn.style = "display:none";
      }

    },

    goDetail(qa_no) {

      this.$router.push({
        name: 'questionDetail',
        params: { "qa_no": qa_no }
      });


    },

    loglist_view() {
      this.amount = this.amount;
    },

    goFirstPage() {
      this.page = 1;
      this.get();
    },

    goBeforePage() {
      if (this.page > 1) {
        this.page = this.page - 1;
      } else {
        alert("첫번째 페이지입니다.");
      }
    },

    thisPage(target) {

      this.page = target.innerHTML;
      this.get();

    },

    goNextPage() {

      if (this.page < this.realEnd) {
        this.page = this.page + 1;
        this.get();
      } else {
        alert("마지막 페이지입니다.");
      }
    },

    goLastPage() {
      this.page = this.realEnd;
      this.get();
    },

    search(target) { //검색버튼 선택

      const userselect = target.previousElementSibling.previousElementSibling.value;
      const usertext = target.previousElementSibling.value;

      if (userselect === "title") {
        this.searchTitle = usertext;
        this.get();
      } else if (userselect === "content") {
        this.searchContent = usertext;
        this.get();
      }


    }

  },
  mounted() {
    //데이터 출력시키기
    this.get();
  }
};
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Jua&family=Noto+Sans+KR:wght@100;300;400;500;700&display=swap');

* {
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  text-decoration: none;
}

/* 문의사항 목록  */

.hy_main {
  display: flex;
  justify-content: center;
  height: auto;
}

.hy_container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

.hy_container h2 {
  margin: 20px 0;
}

.btn_write {
  float: right;
  margin: 10px 0;
  border-radius: 3px;
  font-size: 16px;
  font-weight: 500;
  background-color: orange;
  color: #fff;
  border: none;
  padding: 10px 20px;
}

.hy_view {
  float: left;
  margin: 10px 0;
  padding: 10px 20px;
}


.hy_list {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}


.hy_head {
  background-color: #f5f5f5;
  text-align: center;
}

.hy_head th {
  padding: 1em;
}

.hy_head th:first-child {
  border-top-left-radius: 10px;
}

.hy_head th:last-child {
  border-top-right-radius: 10px;
}

.hy_body tr:nth-child(even) {
  background-color: #f9f9f9;
}

.hy_body td {
  padding: 1em;
  text-align: center;
  border-bottom: 1px solid #ddd;
}

.hy_body td:first-child {
  border-bottom-left-radius: 10px;
}

.hy_body td:last-child {
  border-bottom-right-radius: 10px;
}

.hy_body td a {
  color: #333;
  text-decoration: none;
}

.hy_body td a:hover {
  text-decoration: underline;
}


.hy_serch_box {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
}

.hy_serch_box select, input {
  padding: 5px;
  margin: 5px;
}
.hy_serch_box>button {
  padding: 5px;
  margin: 5px;
}

.hy_serch_box input {
  width: 50%;
  border-radius: 35px;
  border: 1px solid #ddd;
}

.hy_serch_box>button {
  width: 10%;
  background-color: orange;
  border: none;
  color: white;
  font-size: 16px;
}

.hy_page {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.hy_page ul {
  display: flex;
  align-items: center;
  list-style: none;
}

.hy_page li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  font-size: 14px;
  margin: 0 5px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
}

.hy_page li.on {
  font-weight: bold;
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.hy_page i {
  font-size: 14px;
  margin: 0;
  padding: 0;
  line-height: 1;
}

.page_btn {
  display: flex;
  justify-content: center;
  align-items: center;
}</style>
